@font-face {
  font-family: "PT Sans";
  src: url(/src/fonts/PT_Sans/PTSans-Regular.ttf);
  font-weight: 400;
}
@font-face {
  font-family: "Post No Bills Jaffna SemiBold";
  src: url(/src/fonts/PostNoBillsJaffna-SemiBold.ttf);
  font-weight: 700;
}
@font-face {
  font-family: "Playfair Display";
  src: url(/src/fonts/PlayfairDisplay-Regular.ttf);
  font-weight: 400;
}
.header_contact {
  background-color: #000000;
  width: 100%;
  height: 44px;
  padding: 14px 0;
}
.header_contact .header_contact_wrap {
  display: flex;
  justify-content: space-around;
}
.header_contact .header_contact_item {
  color: #969696;
  font-size: 14px;
}
.header_contact .header_contact_item_icon {
  width: 13.36px;
  height: 13.33px;
}
.header_contact .header_contact_item_icon_input {
  display: inline-block;
  position: relative;
  margin-right: 10px;
}
.header_contact .header_contact_item_icon_input_arrow {
  position: absolute;
  margin-top: 3px;
  margin-left: -3px;
}
.header_contact .text_cont {
  display: inline-block;
  padding: 0 10px;
  border-right: 1px solid #969696;
  font-family: PT Sans;
  font-weight: 400;
}
.header_contact .text_cont:nth-child(4) {
  border-right: none;
}

.nav_wrap {
  background-color: #0f0f0f;
  display: flex;
  justify-content: space-around;
  width: 100%;
  height: 80px;
}
.nav_wrap .nav_wrap_title {
  color: #ffffff;
  font-size: 36px;
  text-transform: uppercase;
  margin-top: 20px;
  margin-left: 70px;
  font-family: "Post No Bills Jaffna SemiBold", sans-serif;
  font-weight: 700;
}
.nav_wrap .nav_wrap_menu_item {
  text-transform: uppercase;
  display: inline-block;
  color: #969696;
  font-size: 14px;
  font-family: PT Sans;
  font-weight: 400;
  padding: 31px 20px;
}
.nav_wrap .nav_wrap_menu_item:hover {
  background-color: #1f1f1f;
}
.nav_wrap .nav_wrap_menu_item:nth-child(4) {
  background-color: transparent;
}
.nav_wrap .icons_menu {
  position: relative;
  margin-left: 61px;
}
.nav_wrap .icons_menu_resucle {
  width: 18.33px;
  height: 12.83px;
  margin: -2px;
}
.nav_wrap .icons_menu_ball {
  position: absolute;
  margin-top: 12px;
  margin-left: -7px;
  width: 2.75px;
  height: 2.75px;
}
.nav_wrap .ball2 {
  margin-left: -12px;
  width: 2.75px;
  height: 2.75px;
}
.nav_wrap .icons_menu_search {
  width: 16.5px;
  height: 16.5px;
  margin-left: 44px;
}

.start_page {
  width: 100%;
  min-height: 810px;
  background: url(/src/img/mainBack.png) 50% no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.start_page .start_page_block {
  width: 320px;
  height: 320px;
  border: 4px solid rgb(255, 255, 255);
}
.start_page .start_page_block .start_block_title {
  width: 238px;
  display: inline-block;
  margin-top: 94px;
  margin-left: 40px;
  margin-bottom: 3px;
  text-transform: uppercase;
  color: #ffffff;
  font-family: "Post No Bills Jaffna SemiBold", sans-serif;
  font-weight: 700;
  font-size: 64px;
  font-style: normal;
  text-align: center;
}
.start_page .start_page_block .start_block_title_line {
  border: 1px solid white;
  width: 183px;
  margin: 0 auto;
  margin-bottom: 7px;
}
.start_page .start_page_block .start_block_text {
  text-align: center;
  font-size: 22px;
  text-transform: uppercase;
  color: #ffffff;
  font-family: PT Sans;
  font-weight: 400;
  line-height: 28px;
}
.start_page .start_page_text {
  position: absolute;
  max-width: 730px;
  height: 96px;
  margin-top: 550px;
  font-family: PT Sans;
  text-transform: uppercase;
  font-size: 16px;
  color: #ffffff;
  text-align: center;
  line-height: 24px;
}

.main_page {
  display: flex;
  flex-wrap: wrap;
}
.main_page .about_item_season2020 {
  width: 50%;
  height: 730px;
  background-color: #000000;
}
.main_page .block_title {
  margin-top: 93px;
  margin-left: auto;
}
.main_page .block_title .title_h1 {
  text-align: center;
  font-size: 36px;
  text-transform: uppercase;
  font-family: "Playfair Display";
  color: #ffffff;
}
.main_page .block_title .title_line {
  width: 120px;
  margin: 50px auto;
  border: 1px solid #ffffff;
}
.main_page .products_block_wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  position: relative;
}
.main_page .products_block_wrap .products_block_item {
  width: 30%;
  height: 325px;
  background-color: #191b1c;
}
.main_page .products_block_wrap .products_block_item .item_img {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}
.main_page .products_block_wrap .watch_block_text {
  position: absolute;
  margin-top: 80px;
}
.main_page .products_block_wrap .watch_block_text .watch_block_name {
  font-size: 18px;
  color: #ffffff;
  margin-bottom: 10px;
  line-height: 21px;
}
.main_page .products_block_wrap .watch_block_text .watch_block_price {
  font-size: 16px;
  color: #ffffff;
  line-height: 19px;
}
.main_page .products_block_wrap .watch_block_text .watch_block_name,
.main_page .products_block_wrap .watch_block_text .watch_block_price {
  font-family: "Playfair Display";
}
.main_page .about_item_newCollection {
  width: 50%;
  height: 730px;
  background: url(/src/img/bg_newCollection.png);
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: cover;
}
.main_page .about_item_newCollection .block_title {
  margin: 483px auto;
}
.main_page .about_item_newCollection .block_title .title_line {
  margin-top: 30px;
  margin-bottom: 0px;
}
.main_page .about_item_newCollection .block_title .button_wrap_newCollect {
  display: flex;
  justify-content: center;
}
.main_page .about_item_newCollection .block_title .button_wrap_newCollect .page_info_button {
  margin-top: 30px;
  text-align: center;
  background: transparent;
  font-size: 18px;
  text-transform: uppercase;
  border: 1px solid #ffffff;
  padding: 12px 28px;
  color: #ffffff;
  font-family: "PT Sans";
}
.main_page .about_item_img {
  width: 40%;
  height: 600px;
  background-image: url(/src/img/bg_item_img.png);
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: cover;
}
.main_page .about_item_collect2018 {
  width: 60%;
  height: 600px;
  background-color: #000000;
}
.main_page .about_item_collect2018 .block_title {
  margin-top: 100px;
}
.main_page .about_item_collect2018 .collect2018_text {
  margin: 0px auto;
  width: 471px;
  height: 173px;
  font-size: 14px;
  text-align: center;
  color: white;
  font-family: "Playfair Display";
  line-height: 21px;
}
.main_page .about_item_collect2018 .button_wrap {
  display: flex;
  justify-content: center;
}
.main_page .about_item_collect2018 .button_wrap .collect2018_button {
  margin-top: 65px;
  text-align: center;
  font-size: 18px;
  text-transform: uppercase;
  border: 1px solid #ffffff;
  padding: 12px 28px;
  color: #ffffff;
  background-color: transparent;
  font-family: "PT Sans";
}
.main_page .new_progress {
  width: 100%;
  min-height: 1289px;
  background-color: #000000;
}
.main_page .new_progress .new_progress_wrap {
  display: flex;
  justify-content: center;
}
.main_page .new_progress .products_block_wrap {
  width: 80%;
  height: 962px;
}
.main_page .new_progress .products_block_item {
  width: 24%;
}
@media screen and (max-width: 860px) {
  .main_page .new_progress {
    height: 1900px;
  }
  .main_page .new_progress .products_block_item {
    width: 45%;
    margin-bottom: 80px;
  }
}
.main_page .our_brands {
  width: 100%;
  min-height: 362px;
  background-color: #0F0F0F;
}
.main_page .our_brands .block_title {
  margin-top: 60px;
}
.main_page .our_brands .title_line {
  margin-top: 40px;
}
.main_page .our_brands .brands_img_wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.main_page .our_brands .brands_img_item {
  width: 255;
  height: 94px;
  margin-right: 30px;
  margin-bottom: 80px;
}

.about_us {
  width: 100%;
  min-height: 400px;
  background: url(/src/img/about_us.png);
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: auto 100%;
}
.about_us .footer_wrap {
  width: 100%;
  flex-wrap: wrap;
  min-height: 220px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 80px;
}
.about_us .footer_wrap .footer_item_shop {
  width: 30%;
  height: 219px;
  margin-right: 31px;
}
.about_us .footer_wrap .footer_item_categories {
  width: 15%;
  height: 190px;
  margin-right: 80px;
}
.about_us .footer_wrap .footer_item_mailingList {
  width: 30%;
  height: 200px;
}
.about_us .footer_wrap .footer_item_title {
  font-family: "Playfair Display";
  line-height: 28px;
  font-size: 24px;
  color: #FFFFFF;
  margin-bottom: 40px;
}
.about_us .footer_wrap .footer_item_text {
  font-family: "PT Sans";
  font-size: 14px;
  line-height: 21px;
  color: #FFFFFF;
}
.about_us .footer_wrap .button_wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.about_us .footer_wrap .button_wrap .button_item {
  font-size: 14px;
  padding: 10px 20px;
  background-color: #000000;
  color: #FFFFFF;
  margin-bottom: 15px;
  text-transform: lowercase;
  font-family: "PT Sans";
}
.about_us .footer_wrap .input_wrap {
  margin-top: 37px;
  display: flex;
  flex-wrap: wrap;
}
.about_us .footer_wrap .input_wrap .input_item_email {
  width: 210px;
  height: 40px;
  background: rgba(255, 255, 255, 0.4);
  font-size: 20px;
}
.about_us .footer_wrap .input_wrap .input_item_submit {
  padding: 10px 20px;
  font-family: "PT Sans";
  background: #000000;
  color: #FFFFFF;
  line-height: 21px;
  font-size: 14px;
  text-transform: uppercase;
}
@media (max-width: 363px) {
  .about_us .footer_wrap .input_wrap .input_item_email {
    width: 50%;
  }
  .about_us .footer_wrap .input_wrap .input_item_submit {
    width: 25%;
  }
}
@media (max-width: 1200px) {
  .about_us .footer_wrap {
    height: 550px;
    align-items: flex-start;
    padding-top: 5px;
  }
  .about_us .footer_wrap .footer_item_shop,
.about_us .footer_wrap .footer_item_categories,
.about_us .footer_wrap .footer_item_mailingList {
    width: 50%;
    height: auto;
    margin-right: 0px;
  }
  .about_us .footer_wrap .footer_item_categories {
    margin-right: 10px;
  }
}
@media (max-width: 992px) {
  .about_us .footer_wrap {
    height: 590px;
    align-items: flex-start;
    padding-top: 5px;
  }
  .about_us .footer_wrap .footer_item_shop,
.about_us .footer_wrap .footer_item_categories,
.about_us .footer_wrap .footer_item_mailingList {
    width: 70%;
    height: auto;
    margin-right: 0px;
  }
  .about_us .footer_wrap .footer_item_categories {
    margin-right: 10px;
  }
}
@media (max-width: 768px) {
  .about_us .footer_wrap {
    height: 610px;
    align-items: flex-start;
    padding-top: 5px;
  }
  .about_us .footer_wrap .footer_item_shop,
.about_us .footer_wrap .footer_item_categories,
.about_us .footer_wrap .footer_item_mailingList {
    width: 80%;
    height: auto;
    margin-right: 0px;
  }
  .about_us .footer_wrap .footer_item_categories {
    margin-right: 10px;
  }
}
@media (max-width: 480px) {
  .about_us .footer_wrap {
    height: 650px;
    align-items: flex-start;
    padding-top: 5px;
  }
  .about_us .footer_wrap .footer_item_shop,
.about_us .footer_wrap .footer_item_categories,
.about_us .footer_wrap .footer_item_mailingList {
    width: 90%;
    height: auto;
    margin-right: 0px;
  }
  .about_us .footer_wrap .footer_item_categories {
    margin-right: 10px;
  }
}

.footer_text {
  width: 100%;
  min-height: 50px;
  color: white;
  background-color: #0F0F0F;
  text-align: center;
  padding-top: 17px;
  font-size: 12px;
}

@media (max-width: 1200px) {
  .container .about_item_season2020,
.container .about_item_newCollection,
.container .about_item_img,
.container .about_item_collect2018 {
    width: 100%;
  }
}